.post__card{
     
    margin:10px;
    cursor: pointer; 
    
}
.MuiTypography-root{
    overflow-wrap: break-word !important;
}
.MuiPaper-root{
    min-width: 300px !important;
    max-width:300px !important;
    max-height:160px !important;
    min-height: 160px !important;
}
.post{
    border-radius: 10px;
    transition: 0.5s;
    TRANSITION:transform 200ms ease-in;
}
.post:hover{
    
    transform:scale(1.09)
}
